<template>
  <div class="login-container">
    <div class="login-box">
      <el-form ref="login" :model="login" :rules="loginRules" label-width="80px">
        <el-form-item label="用户名" prop="username">
          <el-input prefix-icon="el-icon-user" placeholder="请输入用户名" v-model="login.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input prefix-icon="el-icon-lock" placeholder="请输入密码" v-model="login.password" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="loginIn">登录</el-button>
          <el-button @click="reset">取消</el-button>
          <el-link class="login-register" type="primary" @click="registerPage=true">注册</el-link>
        </el-form-item>
      </el-form>
    </div>

    <el-dialog title="信息注册" :visible.sync="registerPage" width="30%">
      <el-form ref="register" :rules="registerRules" :model="register" label-width="80px">
        <el-form-item label="用户名" prop="username">
          <el-input prefix-icon="el-icon-user" placeholder="请输入用户名" v-model="register.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input prefix-icon="el-icon-lock" placeholder="请输入密码" v-model="register.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="repassword">
          <el-input prefix-icon="el-icon-lock" placeholder="请输入密码" v-model="register.repassword" show-password></el-input>
        </el-form-item>
        <div class="login-register-btn">
          <el-button type="primary" @click="reg">注册</el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'login',
    data() {
      return {
        registerPage: false,
        login: {
          username: '',
          password: ''
        },
        register: {
          username: '',
          password: '',
          repassword: ''
        },
        loginRules: {
          username: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }],
          password: [{
              required: true,
              message: '请输入密码',
              trigger: 'blur'
            },
            {
              min: 6,
              max: 8,
              message: '密码长度应在6到8个字符',
              trigger: 'blur'
            }
          ],
          repassword: [{
              required: true,
              message: '请输入密码',
              trigger: 'blur'
            },
            {
              min: 6,
              max: 8,
              message: '密码长度应在6到8个字符',
              trigger: 'blur'
            }
          ]
        },
        registerRules: {
          username: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }],
          password: [{
              required: true,
              message: '请输入密码',
              trigger: 'blur'
            },
            {
              min: 6,
              max: 8,
              message: '密码长度应在6到8个字符',
              trigger: 'blur'
            },
            {
              validator: (rules, value, callback) => {
                if (this.register.repassword != '' && this.register.repassword != value) {
                  callback(new Error('密码不一致！'))
                } else {
                  callback();
                }
              },
              trigger: 'blur'
            }
          ],
          repassword: [{
              required: true,
              message: '请输入密码',
              trigger: 'blur'
            },
            {
              min: 6,
              max: 8,
              message: '密码长度应在6到8个字符',
              trigger: 'blur'
            },
            {
              validator: (rules, value, callback) => {
                if (this.register.password != '' && this.register.password != value) {
                  callback(new Error('密码不一致！'))
                } else {
                  callback();
                }
              },
              trigger: 'blur'
            }
          ]
        }
      }
    },
    methods: {
      /**
       * 登录
       */
      loginIn() {
        this.$refs.login.validate(valid => {
          if (!valid) {
            return;
          }
          var formData = new FormData();
          formData.append('username', this.register.username);
          formData.append('password', this.register.password);
          var that = this;
          this.$axios.post('/app/user/loginIn', formData).then(function(res) {
            if (res.data.code == 500) {
              that.$message({
                type: 'error',
                message: res.data.msg,
                duration: 1000
              })
            } else {
              that.$message({
                type: 'success',
                message: res.data.msg,
                duration: 500,
                onClose: function() {
                  that.$router.push({
                    path: '/home'
                  });
                }
              })
            }
          }).catch(function(error) {
            console.log(error);
          })
        })
      },
      reset() {
        console.log(localStorage);
        this.$refs.login.resetFields();
      },
      reg() {
        this.$refs.register.validate(valid => {
          if (!valid) {
            return;
          }
          var formData=this.ParamUtil.combineParam(this.register);
          var that=this;
          this.$axios.post('/app/user/register', formData).then(function(res) {
            if (res.data.code == 500) {
              that.$message({
                type: 'error',
                message: res.data.msg,
                duration: 1000
              })
            } else {
              that.$message({
                type: 'success',
                message: res.data.msg,
                duration: 500,
                onClose: function() {
                    that.registerPage=false;
                }
              })
            }
          }).catch(function(error) {
            console.log(error);
          })
          // for(var i in this.register){
          //   console.log(i);
          // }
          //console.log(this.register)
          //alert(valid);
        });
      }
    }
  }
</script>

<style scoped>
  .login-container {
    background-color: #2b4b6b;
    height: 100%;
  }

  .login-box {
    border-radius: 5px;
    padding: 30px 30px 0 0;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .login-register {
    position: absolute;
    right: 5px;
  }

  .login-register-btn {
    text-align: center;
  }
</style>



